.menu {
  background-color: $blue-color;
  box-sizing: border-box;
  height: 100vh;
  padding: $headerTopPadding 0;
  overflow-y: auto;

  &__top {
    margin-bottom: 48px;
  }

  &__list {
    margin: 0 0 48px;
    padding: 0;
    list-style: none;
  }

  &__item:not(:last-child) {
    margin-bottom: 24px;
  }

  &__link {
    display: inline-block;
    padding-bottom: 8px;
    border-bottom: 1px solid transparent;

    color: $white-color;
    text-decoration: none;
    text-transform: uppercase;

    font-weight: bold;
    font-size: 22px;
    line-height: 22px;
    letter-spacing: 2px;

    transition: border-color $transition-duration;

    &:hover {
      border-color: $white-color;
    }
  }

  &__call {
    display: block;
    width: max-content;

    font-weight: bold;
    font-size: 12px;
    line-height: 16px;
    color: $white-color;
    letter-spacing: 2px;
    text-transform: uppercase;
    text-decoration: none;

    transition: transform $transition-duration;

    &:hover {
      transform: scale(1.1);
    }

    &--phone {
      margin-bottom: 16px;
    }

    &--action {
      padding-bottom: 8px;
      border-bottom: 1px solid $white-color;
    }
  }
}
